import React, { useState } from "react";
import './Home.css';
import emailjs from '@emailjs/browser';
import img from '../../img/kali4.jpg';
import King from '../../img/King.png';
import { Button, TextField } from "@mui/material";

const Home = () =>{
    
    const [show ,setShow] = useState(false);
    const [sco ,setSco] = useState();
    const [values ,setValues] = useState([]);
    const [error,setError]=useState([]);
    const [isSubmit,setIsSubmit]=useState(false);

    const submit = (e) =>{
        // e.preventDefault();
        setError(Validate(values));
        setIsSubmit(true);
        if(Object.keys(error).length !== 0)
        {
            // var templateParams = {
            //       name:values.name,
            //       phone:values.phone,
            //       email:values.email,
            //       subject:values.subject,
            //       message:values.message
            //    }
            //     emailjs.send('service_kp55o9z', 'template_9kxnnlg',templateParams, 'BWP5VoMuHTSLMvlS6')
            //      .then((result) => {
            //             // templateParams({name:"",phone:"",email:"",subject:"",message:""})
            //             setValues({name:"",phone:"",email:"",subject:"",message:""});
            //         }, (error) => {
            //             console.log(error.text);
            //         });
                    alert("Message send successfully..");
                    setValues({name:"",phone:"",email:"",subject:"",message:""});
                    e.target.reset();
        }
        else
        {
            alert("Fill the fields");
        }
        e.target.reset();
    }

    const onValidate = () =>{
        if(Object.keys(error).length !== 0)
        {
          setError(Validate(values));
          setIsSubmit(false);
          
        }
      }

    const Validate = (values) =>{
        const errors ={};
        const refx =  /^[^\s@]+@[^\s@]+\.[^\s@]{2,}$/i;

        if(!values.name)
        {
            errors.name="Field is required ..";
        }
        if(!values.phone)
        {
            errors.phone="Field is required ..";
        }
        if(!values.email)
        {
            errors.email="Field is required ..";
        }else if(!refx.test(values.email))
        {
            errors.email="Incorrect email format.."
        }
        if(!values.subject)
        {
            errors.subject="Field is required ..";
        }
        if(!values.message)
        {
            errors.message="Field is required ..";
        }

        return errors;
    }

    const clicked = () =>{
          setShow(true);
          setSco(window.scrollY+50);
    }

    console.log(sco)

    function setFixed()
    {
        if(window.scrollY>=sco)
        {
            setShow(false);
        }
    }
    window.addEventListener("scroll",setFixed);
   
    return(
        <div className="H-container" id="Profile">
{/* -------------  Left Side  ------------- */}
            <div className="H-left">
                <div className="H-left-1">
                    <h4>Hi There !!</h4>
                    <div style={{display:'flex',flexDirection:'row',gap:'2vw'}}><h3>I'm</h3><h1>Kaliyappan</h1></div>
                    <p> IAm a web designer and web developer ,then i creating high quality websites for all types of business,</p>
                    <div className="btns">
                        <button onClick={()=>clicked()}>Touch Me</button>
                        <button>Get Resume</button>
                    </div>
                </div> 
            </div>


{/* -------------  Right Side  ------------- */}

            <div className="H-right">
                <img src={img}/>
                {show ? <div className="forms" id="forms">
                    <TextField variant="outlined" required error={error.name} fullWidth size="small" label="Name" value={values['name']} onChange={(e)=>setValues({...values,'name':e.target.value})} onInput={onValidate}/><br/><br/>
                    <TextField variant="outlined" required error={error.phone} fullWidth size="small" label="Phone Number" value={values['phone']} onChange={(e)=>setValues({...values,'phone':e.target.value})} onInput={onValidate}/><br/><br/>
                    <TextField variant="outlined" required error={error.email} fullWidth size="small" label="email" value={values['email']} onChange={(e)=>setValues({...values,'email':e.target.value})} onInput={onValidate}/><br/><br/>
                    <TextField variant="outlined" required error={error.subject} fullWidth size="small" label="Subject" value={values['subject']} onChange={(e)=>setValues({...values,'subject':e.target.value})} onInput={onValidate}/><br/><br/>
                    <TextField variant="outlined" required error={error.message} fullWidth size="small" label="Say something" value={values['message']} onChange={(e)=>setValues({...values,'message':e.target.value})} onInput={onValidate}/><br/><br/>
                    <Button variant="contained" onClick={()=>submit()}>Send</Button>
                </div>:""}
            </div>
        </div>
    )
}
export default Home;