import React, { useState ,useEffect} from 'react';
import './Navbar.css';
import { Anchor } from 'antd';
import Aos from 'aos';
import 'aos/dist/aos.css';
import King from '../../img/King.png';
import ListIcon from '@mui/icons-material/List';
import CloseIcon from '@mui/icons-material/Close';
import kali from '../../img/kaaali.jpg';
import EjectIcon from '@mui/icons-material/Eject';
import MarkEmailReadRoundedIcon from '@mui/icons-material/MarkEmailReadRounded';
import TtyRoundedIcon from '@mui/icons-material/TtyRounded';
import Person3Icon from '@mui/icons-material/Person3';
import InfoIcon from '@mui/icons-material/Info';
import ManageAccountsIcon from '@mui/icons-material/ManageAccounts';
import SchoolIcon from '@mui/icons-material/School';
import HomeRepairServiceIcon from '@mui/icons-material/HomeRepairService';
import ContactPageIcon from '@mui/icons-material/ContactPage';
import CopyrightIcon from '@mui/icons-material/Copyright';

const { Link } = Anchor;
const Navbar = () =>{

    const [show,setShow] = useState(false);
    const [sco,setSco] = useState();

    useEffect(()=>{
        Aos.init({duration:1000});
      },[])

    const setClick = () =>{
        setShow(!show);
        setSco(window.scrollY);
    }

    function setFixed()
    {
        if(window.scrollY<=sco || window.scrollY>=sco)
        {
            setShow(false);
        }
    }
    window.addEventListener('scroll',setFixed);

    return(
        <>
        {/* <div style={{width:'100%',height:'auto',display:'flex',flexDirection:'row'}}>
              <div style={{width:'50%',display:'flex',flexDirection:'row',fontSize:'0.7vw',fontWeight:'700',fontStyle:'italic',gap:'40px',justifyContent:'center',alignItems:'center',alignContent:'center',textAlign:'center'}}>
                <p style={{display:'flex',flexDirection:'row',textAlign:'center',justifyContent:'center',alignItem:'center'}}><MarkEmailReadRoundedIcon/>kalisoft117@gmaail.com</p>
                <p><TtyRoundedIcon/>9791745073</p>
              </div>
              <div style={{width:'50%',display:'flex',flexDirection:'row',fontSize:'0.7vw',fontWeight:'700',fontStyle:'italic',gap:'10px',justifyContent:'right',color:'rgb(8,81,112)',alignItems:'center',paddingRight:'10vw'}}>
                 <FacebookRoundedIcon/>
                 <WhatsAppIcon/>
                 <InstagramIcon/>
                 <TwitterIcon/>
              </div>
        </div> */}
        <div className="N-container">
            <div className="N-left">
               <img src={King}/>
            </div>
            <div className="N-right">
                <Anchor affix={false}>
                    <div style={{display:'flex',flexDirection:'row',gap:'2vw',fontSize:'1vw',padding:'1vw',color:'white'}}>
                        <Link href="#Profile" title="Profile" />
                        <Link href="#About" title="About" />
                        <Link href="#Services" title="Services" />
                        <Link href="#Education" title="Education"/>
                        <Link href="#Works" title="Works" />
                        <Link href="#Contact" title="Contact" />
                    </div>
                </Anchor>
            </div>
        </div>
        <div className="N1-container">
            <div className="N-left">
               <img src={King}/>
               <p style={{paddingLeft:'8vw',position:'relative',top:'-4vw',color:'rgb(177, 176, 176)'}}>Kaliyappan</p>
               <p style={{paddingLeft:'10vw'}}>Hi There !!</p>
               <div style={{paddingLeft:'10vw',display:'flex',gap:'2vw',flexDirection:'row'}}>
                    <p>I'm</p>
                    <p style={{fontSize:'10vw',color:"rgb(8,81,112)",textShadow:'0 2px 2px black',fontFamily:'Denk one'}}>Kaliyappan</p>
               </div>
            </div>
            <div className="N1-right">
                <div className='listIcon' onClick={()=>setClick()}>
                    {show?<CloseIcon style={{fontSize:'8vw'}}/>:
                    <ListIcon style={{fontSize:'8vw'}}/>}
                </div>
                {show?<Anchor affix={false}>
                    <div className='menuList' data-aos="fade-left">
                        <div>
                             <img src={kali}/>
                             <p><MarkEmailReadRoundedIcon/>kalisoft117@gmail.com</p>
                             <p><TtyRoundedIcon/>9791745073 | 9626345073</p>
                        </div>
                        <div>
                            <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<Person3Icon/><Link href="#Profile" title="Profile" /></p>
                            <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<InfoIcon/><Link href="#About" title="About" /></p>
                            <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<SchoolIcon/><Link href="#Education" title="Education"/></p>
                            <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<ManageAccountsIcon/><Link href="#Services" title="Services" /></p>
                            <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<HomeRepairServiceIcon/><Link href="#Works" title="Works" /></p>
                            <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<ContactPageIcon/><Link href="#Contact" title="Contact" /></p>
                        </div>
                        <div>
                            <CopyrightIcon/><p>No Copyright Kaliyappan profile developed by KaliSanthosh softTech....</p>
                        </div>
                    </div>
                </Anchor>:""}
            </div>
            <Anchor affix={false}>
            <div className="backTop">
                <Link href="#Profile" title={<EjectIcon style={{fontSize:'8vw'}}/>} />
                {/* <a href="#Profile">
                <EjectIcon style={{fontSize:'8vw'}}/>
                </a> */}
            </div>
            </Anchor>
        </div>
        </>
    )
}
export default Navbar;